.switch {
  width: 40px;
  height: 20px;
  margin-right: 14px;
  display: inline-block;
  position: relative;
}

.switch__input {
  width: 0;
  height: 0;
  opacity: 0;
}

.switch__slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch__slider:before {
  content: "";
  cursor: pointer;
  height: 18px;
  width: 18px;
  background-color: #fff;
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

.switch__input:focus + .switch__slider {
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.switch__input:checked + .switch__slider {
  background: linear-gradient(#4f2ee8, #686868);
}

.switch__input:checked + .switch__slider:before {
  transform: translateX(20px);
}

