.container {
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  text-align: start;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}
@media screen and (min-width: 1440px) {
  .container {
    display: flex;
    max-width: 1440px;
  }
}
.section {
  padding-top: 77px;
}
.category {
  margin-bottom: 40px;
  margin-left: 40px;

  color: $color-black;
  font-family: DM Sans;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.08;
  letter-spacing: -1.92px;
}
.card-set {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  gap: 40px;
}
.card-set-item {
  width: 335px;
  height: 485px;
}
@media screen and (min-width: 768px) {
  .card-set-item {
    width: 218px;
    height: 316px;
  }
}

@media screen and (min-width: 1440px) {
  .card-set-item {
    width: 180px;
    height: 256px;
  }
}
.book-img {
  margin-bottom: 14px;
}
@media screen and (min-width: 768px) {
  .card-set {
    row-gap: 40px;
    column-gap: 25px;
  }
}
@media screen and (min-width: 1440px) {
  .card-set {
    row-gap: 40px;
    column-gap: 24px;
  }
}

.book-title {
  color: $color-black;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.13;
  letter-spacing: -0.64px;
  max-width: 180px;
}

.book-author {
  color: $color-grey;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
  letter-spacing: -0.48px;
}

.container-home-page {
  max-width: 375px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
}

.wrapper {
  margin-bottom: 40px;
}

.container-books {
  width: 335px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 40px;
  margin-bottom: 40px;
}

.js-list-books {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: flex-start;
}

.title-book {
  width: 335px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.19;
  letter-spacing: -1.28px;
  margin-bottom: 40px;
}

.js-category-name {
  color: $color-grey;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
  letter-spacing: 0.42px;
  text-transform: uppercase;
  margin-bottom: 18px;
}

.js-named-book {
  color: $color-black;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.13;
  letter-spacing: -0.64px;
  text-transform: uppercase;
  margin-top: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.js-author-book {
  color: $color-grey;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
  letter-spacing: -0.48px;
  margin-top: 4px;
}

.marker {
  color: $color-violet;
}

.img-books {
  width: 335px;
  height: 485;
  border-radius: 8px;
}

.js-btn-books {
  width: 124px;
  height: 46px;
  color: $color-black;

  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 40px;
  border: 2px solid $color-violet;
  margin-left: auto;
  margin-top: 8px;
  padding: 14px 28px;

  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  letter-spacing: -0.14px;
  text-transform: uppercase;
  white-space: nowrap;
}

.js-btn-books:hover,
.js-btn-books:focus {
  cursor: pointer;
  background-color: $color-violet;
  color: $color-white;
}

.js-item-book {
  width: 335px;
  list-style: none;
  position: relative;
}

.js-item-book:last-child {
  margin-bottom: 0px;
}

@media screen and (min-width: 768px) {
  .container-home-page {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container-home-page {
    max-width: 1440px;
    margin: 0;
    padding: 0;
  }
}
